/* eslint-disable no-lone-blocks */
<template>

<div class="jumbotron container">
    <h1 class="display-4">Hızlı Yazma Yarışması</h1>
    <p class="lead">
        Ne Kadar Hızlı Klavye Kullandığını Test et!</p>
    <hr class="my-4">

    <div v-if="finished" class="alert alert-primary sonuc">
      <h1>Süre Bitti</h1>
      <p class="display-4"> {{dks}} DKS </p>
      <span>Doğruluk Yüzdesi : %{{truepercent}}</span>
      <span>Doğru Kelime : {{trueCount}} </span>
      <span>Yanlış Kelime : {{falseCount}} </span>
      <button @click="NewGame" class="btn btn-success btn-block btn-lg mt-3">Yeni Oyun</button>
    </div>

    <div v-else class="kapsayici">
    <div class="card mb-2">
        <div class="card-body">
            <span v-for="(word,key) in words.filter((data,index)=>index<10)" :key="key" v-bind:class="key==0? writingWordControl :null" class="words ml-2">
                {{word}}
            </span>
        </div>
    </div>

    <div class="card">
        <div class="card-body bg-light">
          <div class="row d">
            <div class="col-md-9 input-group input-group-lg">
              <input type="text" class="form-control" v-model="writingWord">
            </div>
            <div class="col-md-2 input-group-append">
              <button class="btn mr-2 rounded btn-secondary" disabled type="button">{{timer}} sn.</button>
            </div>
            <div class="col-md-1 input-group-append">
              <button :disabled="start" class="btn btn-secondary" type="button" @click="getWords">Yenile</button>
            </div>
          </div>
        </div>
    </div>
    </div>

</div>
</template>

<script>

import Wordlist from '@/assets/words.json'
export default {
  data () {
    return {
      words: [],
      writingWord: null,
      isTrue: true,
      trueCount: 0,
      falseCount: 0,
      timer: 60,
      interval: false,
      start: false,
      finished: false,
      kelimeler: Wordlist
    }
  },

  mounted () {
    this.getWords()
  },
  watch: {
    writingWord (val) {
      if (!val || val === ' ') {
        this.writingWord = ''
        return
      }
      if (!this.start) this.toggleTimer()
      const word = this.words[0].slice(0, val.length).toUpperCase()

      const UserWord = val.replace(' ', '').toUpperCase()

      this.isTrue = word === UserWord

      if (val.indexOf(' ') !== -1) {
        this.isTrue ? this.trueCount++ : this.falseCount++
        this.words.splice(0, 1)
        this.writingWord = ''
        this.isTrue = true
      }
    }
  },

  computed: {
    // eslint-disable-next-line vue/return-in-computed-property
    writingWordControl () {
      return this.isTrue ? 'writing-word' : 'writing-word bg-danger'
    },
    dks () {
      return 300 - this.words.length
    },
    truepercent () {
      const percent = (100 / this.dks)
      const yuzde = (percent * this.trueCount).toFixed(2)
      return isNaN(yuzde) ? 0 : yuzde
    }
  },

  methods: {
    NewGame () {
      this.getWords()
      this.finished = false
      this.timer = 60
      this.isTrue = true
      this.start = false
    },

    getWords () {
      this.words = this.kelimeler.sort(() => Math.random() - 0.5).splice(0, 300)
    },

    toggleTimer () {
      this.start = true
      this.interval = setInterval(this.RecursiveTimeProcess, 1000)
    },

    RecursiveTimeProcess () {
      if (this.timer === 0) {
        clearInterval(this.interval)
        this.finished = true
      }
      this.timer--
    }
  }

}

</script>

<style>

.sonuc{
  display: flex;
  flex-direction: column;
  margin-bottom: 2px;
}

.words {
    font-size: 25px;
    font-weight: 400;
}

.writing-word {
    background-color:slategrey;
    color:white;
    padding: 8px;
    border-radius: 5px;
}

@media only screen and (max-width: 600px) {
  .d{
    display: flex;
    flex-direction: row;
    row-gap: 10px;
  }
}

@media only screen and (max-width: 900px) {
  .d{
    display: flex;
    flex-direction: row;
    row-gap: 10px;
  }
}
</style>
